@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}


.home,
.developers,
.products,
.joinus {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.overlay-text {
  position: absolute;
  top: 80px;
  font-weight: bold;
  text-align: right;
  font-size: 20px;
  display: inline-block;
  font-size: 150px;

}

.overlay-text,.background-container{
  color: black;
  animation: fadeIn linear 1.5s;
  -webkit-animation: fadeIn linear 1.5s;
  -moz-animation: fadeIn linear 1.5s;
  -o-animation: fadeIn linear 1.5s;
  -ms-animation: fadeIn linear 1.5s;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}


label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}
input[type=text], input[type=email], textarea{
  border: 2px solid #000;
  border-radius: 3px;
  padding: 10px;
  width: 300px;
  margin-bottom: 10px;
}
button {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 10px;
  font-weight: bold;
  border-radius: 3px;
}



img {
  width: 100%;
  height: 100%;
}

.home{
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  font-size: 100px;
}

.developers {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  font-size: 100px;
}

.products {
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
  font-size: 100px;
}

.joinus {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
  font-size: 100px;
}

h2 {
  text-align: center;
  margin: 250px;
  margin-bottom: 300px;
  font-size: 4rem;
}

