}

.YouTube{
  display: flex;
  flex-direction: column;
  margin: auto;
}

.downloadApp{

    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-evenly;
    padding-top: 3vh;

}

 .vyt{
   text-align: center;
 }

