  }
  .dVox-Card{

    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-around;
    align-self: flex-end;
    height : -webkit-min-content;
    height : min-content;  

    height: min-content;
    width: -webkit-min-content;
    width: min-content;
    margin-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
  
  
    transition: 0.1s ease-in-out;
    font-size: 16px;
    margin-bottom: 100px;
    border-radius: 8px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.08);
  }


